"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { ChapterEditor } from "@/components/dashboard/editor/chapter/chapter-editor"
import { Project, Chapter } from "@/types/editor"
import { recentProjects, chaptersData } from "@/data/editor-mock-data"

interface ChapterEditorPageProps {
  novelId: string
  chapterId: string
}

export function ChapterEditorPage({ novelId, chapterId }: ChapterEditorPageProps) {
  const router = useRouter()
  const [novel, setNovel] = useState<Project | null>(null)
  const [chapter, setChapter] = useState<Chapter | null>(null)
  const [previewMode, setPreviewMode] = useState<"editor" | "reader">("editor")

  useEffect(() => {
    // Find the novel by ID
    const foundNovel = recentProjects.find(p => p.id === parseInt(novelId))
    if (foundNovel) {
      setNovel(foundNovel)
      
      // Find the chapter by ID
      const foundChapter = chaptersData.find(c => c.id === parseInt(chapterId))
      if (foundChapter) {
        setChapter(foundChapter)
      } else {
        // Chapter not found - redirect to novel management
        router.push(`/dashboard/editor/${novelId}`)
      }
    } else {
      // Novel not found - redirect to editor list
      router.push('/dashboard/editor')
    }
  }, [novelId, chapterId, router])

  const handleBack = () => {
    router.push(`/dashboard/editor/${novelId}`)
  }

  const handleSave = (updatedChapter: Chapter) => {
    setChapter(updatedChapter)
    // Here you would typically save to your backend/database
    console.log('Chapter saved:', updatedChapter)
  }

  if (!novel || !chapter) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="text-center">
          <h2 className="text-xl font-semibold text-muted-foreground">
            {!novel ? 'Novel not found' : 'Chapter not found'}
          </h2>
          <p className="text-sm text-muted-foreground mt-2">
            {!novel 
              ? "The novel you're looking for doesn't exist." 
              : "The chapter you're looking for doesn't exist."
            }
          </p>
        </div>
      </div>
    )
  }

  return (
    <ChapterEditor
      chapter={chapter}
      novel={novel}
      onBack={handleBack}
      onSave={handleSave}
      previewMode={previewMode}
    />
  )
}
